
.homeWrap {
    .article2 {
        margin-top: -6px;
        height: 756px;
        padding-top: 112px;
        padding-bottom: 80px;
        text-align: center;
        h2 {
            font-size: 42px; font-weight: 100; color: #3c3c3c;
            span { font-weight: 900; color: #90470c }
            &:after {
                content:''; width: 100px; height: 1px;
                display: block; background: #3c3c3c;
                margin: 51px auto 0;
            }
        }
        h3 {
            margin-top: 53px; margin-bottom: 45px;
            font-size: 26px; font-weight: 100;
            span { font-weight: 500; }
        }
        .slide {
            height: 281px !important;
            ul {
                li {
                    display: inline-block;
                    a {
                        p { 
                            font-family: 'Noto Sans KR', sans-serif;
                            display: flex;
                            justify-content: center;
                        }
                        p:nth-child(2) {
                            font-size: 20px; font-weight: 900;
                            color: #693a13; margin-top: 20px;
                        }
                        p:nth-child(3) {
                            font-size: 15px; color: #cc8e5d;
                        }
                    }
                }
                &.cpack1{
                    max-width: 900px; margin: 0 auto;
                    li { width:30%; margin: 0 1.5%; }
                }
                &.cpack2{
                    max-width: 1290px; margin: 0 auto;
                    li { width:23%; margin: 0 1%; }
                }
                &.cpack3{
                    max-width: 900px; margin: 0 auto;
                    li { width:30%; margin: 0 1.5%; }
                    img { width: 217px;}
                }
            }
        }
    }
    .article3 {
        .factory {
            position: relative;
            max-width: 1180px;
            margin: 0 auto;
            padding: 0 4%;
            margin-bottom: 120px;
            color: #fff;
            h2 {
                padding-top: 90px;
                font-size: 30px;
                font-weight: 900;
                line-height: 2;
                p {
                    text-indent: -9999px;
                }
            }
            & > p {
                margin-top: 10px;
                font-size: 17px;
                line-height: 1.4;
            }
            .df_btn {
                margin: 36px 0;
                a {
                    font-weight: 500;
                    font-family: 'Noto Sans KR', sans-serif;
                    display: inline-block;
                    border: 1px solid #fff;
                    width: 188px; height: 42px;
                    line-height: 42px;
                    text-align: center;
                    margin: 0 5px;
                }
                a:hover {
                    background: #fff;
                    color: #3c3c3c;
                }
            }
            & > img {
                position: absolute;
                bottom: -120px; right: -150px;
                max-width: 860px;
            }
        }
    }
    .article4 {
        height: 500px;
        text-align: center;
        color: #fff;
        h2 {
            padding-top: 112px;
            font-size: 42px; font-weight: 100;
            span { font-weight: 900; }
            &:after {
                content:''; width: 100px; height: 1px;
                display: block; background: #fff;
                margin: 51px auto 0;
            }
        }
        h3 {
            margin-top: 53px; margin-bottom: 45px;
            font-size: 26px; font-weight: 100;
            span { font-weight: 500; }
        }
        & > a {
            display: inline-block;
            border: solid 1px #FFF;
            width: 188px;
            height: 42px;
            line-height: 42px;
            font-size: 15px;
            font-weight: 500;
            font-family: 'Noto Sans KR', sans-serif;
        }
    }
}