.title {
    height:850px; 
    color: #fff;
    h1 {
        padding: 226px 0 75px;
        font-size: 48px;
        display: flex;
        justify-content : center;
    }
    p { text-align: center; }
    & > p:nth-of-type(1) {
        font-family: 'NanumSquare', sans-serif;
        font-size: 38px; margin-bottom: 15px;
    }
    & > p:nth-of-type(2) {
        font-family: 'NanumSquare', sans-serif;
        font-size: 18px; color: #b9b8b8;
    }
}
.outerbox {
    margin: 0 58px;
    display: flex;
    justify-content: center;
    align-items: center;
    .innerbox {
        margin: 100px 0;
        padding: 30px 0 70px;
        border: 1px solid #ebebeb;
        width: 100%;
        max-width: 1180px;
        overflow: hidden;
        .joincom {
            width: 100%;
            max-width: 680px;
            margin: 0 auto;
            border-bottom: 1px solid #ebebeb;
            text-align: center;
            padding: 44px 0 50px;
            & > h2 { 
                font-family: 'NanumSquare', sans-serif;
                font-size: 28px; color: #202022;
                line-height: 1.4;
                font-weight: normal;
            }
            .bugatxt {
                padding-top: 7px; font-size: 18px;
                color: #464646; line-height: 1.7;
                font-family: 'Nanum Gothic', sans-serif;
                font-weight: bold;
            }
            ul.mailform {
                padding-top: 44px;
                li {
                    display: inline-block;
                    height: 58px;
                    line-height: 58px;
                    width: 31.1%;
                    input {
                        height: 58px; font-size: 15px;
                        border-radius: 3px;
                        border: 1px solid #ccc;
                        color: #707070;
                        padding-left: 20px;
                    }
                    &.emailat { width: 5%; text-align: center; }
                    &.email1 > input::placeholder {
                        font-weight: bold; color: #acacac
                    }
                    &.emailselect {
                        height: 58px;
                        font-size: 15px;
                        margin-left: 1%;
                    }
                    select {
                        margin-left: 1%;
                        height: 58px;
                        font-size: 15px;
                        width: 100%;
                        padding: 5px;
                        padding-left: 15px;
                        color: #707070;
                        border-radius: 3px;
                        border: 1px solid #ccc;
                        font-weight: bold;
                    }
                }
            }
            h3.join_code {
                padding-top: 40px;
                text-align: left;
                color: #464646;
                line-height: 1.7;
                font-weight: 600;
                font-size: 15px;
                a {
                    font-size: 20px;
                    display: inline-block;
                    padding-left: 37px;
                    font-weight: 600;
                    margin-right: 5px;
                }
            }
            .join_check_list {
                padding-top: 20px;
                color: #464646;
            }
        }
        .join_btn {
            margin: 50px auto;
            display: flex;
            justify-content: center;
            .bluebtn {
                height: 68px;
                line-height: 68px;
                display: block;
                text-align: center;
                font-size: 18px;
                font-weight: 600;
                color: #fff;
                background: #002f6c;
                border-radius: 3px;
                width: 500px;
            }
        }
    }
    
}